.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 100vw;
  height: 100vh;
  // overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.route-wrapper {
  position: relative;
  & > div {
    position: absolute;
  }
}

.room {
  // overflow: hidden;
  width: 100vw;
  max-width: 1110px;
  height: 100vh;

  .row {
    padding-top: 10%;
    display: flex;
    justify-content: space-evenly;
  }
  
  .item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20%;
    padding: 5%;

    .item {
      width: 100%;
      height: auto;
      transition: transform 0.2s;
      padding-bottom: 25%;
      &:hover {
        transform: scale(1.5);
      }
    }
    .person {
      width: 50%;
    }
  }
}

.back-button {
  position: absolute;
  top: 5vw;
  left: 5vw;
  transition: transform 0.2s;
  
  img {
    height: auto;
    width: 10vw;
  }
  
  &:hover {
    transform: scale(1.5);
  }
}